// Dynamic scroll shadows controlled by JavaScript (see static/js/scroll-shadows.js)
// Ideally this would be controlled by animation-timeline
// but this isn't supported by Firefox 147 to this day.
// https://developer.mozilla.org/en-US/docs/Web/CSS/Reference/Properties/animation-timeline
// https://daverupert.com/2023/08/animation-timeline-scroll-shadows/
//
// Note: The @mixin scroll-shadows is defined in _mixins.scss

// From _code.scss - Markdown code blocks (general pre elements)
.post.md {
  pre {
    @include scroll-shadows($scroll-shadow-color);
  }
}

// From _asciidoctor.scss - AsciiDoc code blocks
.post.adoc {
  .literalblock pre, .listingblock > .content > pre {
    @include scroll-shadows($scroll-shadow-color);
  }
}

// From _base.scss - wrapTable shortcode
// Apply shadows to table-wrapper (which doesn't scroll)
// while admonitionblock or table inside does the scrolling (like pre/code)
.table-wrapper {
  &:has(> table:only-child),
  &:has(> .admonitionblock) {
    @include scroll-shadows($scroll-shadow-color);

    // Round corners of the shadow pseudo-elements
    &.has-scroll-shadow-right::after {
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
    }

    &.has-scroll-shadow-left::before {
      border-top-left-radius: 5px;
      border-bottom-left-radius: 5px;
    }
  }
}
