@mixin transform($string){
	-webkit-transform: $string;
	-moz-transform: 	 $string;
	-ms-transform: 		 $string;
	-o-transform: 		 $string;
}
@mixin rotate($deg){
	-webkit-transform: rotate($deg);
	-moz-transform: 	 rotate($deg);
	-ms-transform: 		 rotate($deg);
	-o-transform: 		 rotate($deg);
}

@mixin link-no-decoration() {
	border-style: none;
	text-decoration: none;
}

@mixin link-hover() {
	color: $link-hover-color;
	border-bottom: 1px dotted $gray-4;
	text-decoration: none;
	background: transparent;
	background-color: transparent;
	word-wrap: break-word;
}

// Scroll shadows mixin using pseudo-elements for overlay effect
@mixin scroll-shadows($color) {
  position: relative;

  &.has-scroll-shadow-left::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 8px;
    background: linear-gradient(to right, $color, transparent);
    pointer-events: none;
    z-index: 1;
  }

  &.has-scroll-shadow-right::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 8px;
    background: linear-gradient(to left, $color, transparent);
    pointer-events: none;
    z-index: 1;
  }
}
